*,*::before,*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #343a40;
  height: 100vh;

}
.container-fluid{
  min-height: 90vh;

}
.calculator {
  border: 5px solid rgba(189, 183, 107, 0.767);
  border-radius: 15px;
}

.calculator-screen {
  width: 100%;
  border: none;
  background-color: #252525;
  color: #fff;
  text-align: right;
  padding-right: 20px;
  padding-left: 10px;
  font-size: 4rem;
  border-radius: 8px 8px 0 0;
}

button {
  height: 60px;
  font-size: 2rem!important;
}

.equal-sign {
  height: 100%;
  grid-area: 2 / 4 / 6 / 5;
}

.calculator-keys {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 15px;
  padding: 20px;
}
.red{
  color: red;
}
.page-footer{
  display: grid;
  place-items: center;
  min-height: 10vh;
}
